import { Component } from 'react';
import { StyleSheet, View, Image, ScrollView, TouchableOpacity, Text, Alert, Platform, Dimensions } from 'react-native';
import { AntDesign, Ionicons, Entypo, MaterialCommunityIcons, Fontisto, FontAwesome, MaterialIcons } from '@expo/vector-icons';
import moment from 'moment';
import intl from 'react-intl-universal';
import Constants from 'expo-constants';
import theme from '../assets/theme';
import wall from '../assets/wall-culina.jpg';

export default class Secondary extends Component {
  constructor(props) {
    super(props);
    this.state = {
      home: null,
      selectedTable: null,
      tableItems: [],
    };
  }

  componentDidMount() {
    if (Platform.OS === 'web') {
      this.broadcastChannel = new BroadcastChannel('bistro');
      this.broadcastChannel.onmessage = (event) => {
        this.setState({
          home: event.data.home,
          selectedTable: event.data.selectedTable,
          tableItems: event.data.tableItems
        });
      };
    }
  }

  componentWillUnmount() {
    if (Platform.OS === 'web') {
      this.broadcastChannel.close();
    }
  }

  doTranslateTableColor(item) {
    if (!item) return theme.grey9;
    if (item.adhocItem) return theme.adhoc;
    if (item.sectionId === 'SYS') {
      if (item.statusFlg === 'D') {
        return theme.paid;
      } else {
        if (item.transType === 'A') {
          return theme.grey5;
        } else {
          return theme.deposit;
        }
      }
    } else {
      if (item.statusFlg === 'A') {
        return theme.vacant;
      } else if (item.statusFlg === 'B') {
        return theme.reserved;
      } else if (item.statusFlg === 'C') {
        return theme.occupied;
      } else if (item.statusFlg === 'D') {
        return theme.paid;
      } else if (item.statusFlg === 'E') {
        return theme.unavailable;
      }
    }
  }

  doTranslateTableStatus(item) {
    if (item.sectionId === 'SYS') {
      if (item.transType === 'A') {
        return intl.get('sales');
      } else if (item.transType === 'G') {
        if (item.homeDelivery === 'Y') {
          return intl.get('deposit') + 'DLY';
        } else {
          return intl.get('deposit');
        }
      }
    } else {
      return intl.get(`${item.statusFlg}`).d('Unknown');
    }
  }

  doTranslateTableItemColor(item) {
    if (item.fireFlg === 'Y') {
      return theme.reserved;
    }
    if (item.confirmFlg === 'Y') {
      return theme.occupied;
    }
    return theme.vacant;
  }

  render() {
    const { route, navigation } = this.props;
    const { home, selectedTable, tableItems, } = this.state;

    return (
      <View style={{ flex: 1, flexDirection: 'row' }}>
        <View style={{ width: 10, backgroundColor: theme.grey8 }}>
          <View style={{ marginTop: 30, height: 100, backgroundColor: this.doTranslateTableColor(selectedTable) }}></View>
        </View>
        <View style={{ flex: 1, backgroundColor: 'white' }}>
          <TouchableOpacity style={{ marginHorizontal: 10, height: 20, flexDirection: 'row', alignItems: 'flex-end', justifyContent: 'space-between' }}
            onPress={() => navigation.goBack()}>
            <Text style={styles.footerText}>{home?.userName}</Text>
            <Text style={styles.footerText}>{home?.locName}</Text>
          </TouchableOpacity>
          <View style={{ margin: 7, marginLeft: 0, flexDirection: 'row', justifyContent: 'space-between' }}>
            <View style={{ margin: 3, flex: 1, flexDirection: 'row', }}>
              {
                selectedTable &&
                <View style={{ marginLeft: 0, aspectRatio: 1, backgroundColor: this.doTranslateTableColor(selectedTable), justifyContent: 'center', alignItems: 'center' }}
                >
                  <Text style={styles.tableUpperBand} numberOfLines={1}>{selectedTable.vipName || ''}</Text>
                  <Text style={{ fontFamily: 'varela-round', color: theme.black8, fontSize: 24, letterSpacing: 1 }} numberOfLines={1}>{selectedTable.tableId}</Text>
                  <Text style={styles.tableLowerBand} numberOfLines={1}>{this.doTranslateTableStatus(selectedTable)}</Text>
                </View>
              }
            </View>
            <View style={{ margin: 3, height: 100 }}>
            </View>
          </View>
          <View style={{ flexDirection: 'row', marginVertical: 3, borderLeftWidth: 5, borderLeftColor: 'white' }}>
            <View style={{ width: 50, height: 20, alignItems: 'center', justifyContent: 'center', borderRightColor: theme.paid }}>
              <Text style={[styles.footerText, { fontSize: 12 }]}>{intl.get('qty')}</Text>
            </View>
            <View style={{ flex: 1, justifyContent: 'center' }}>
              <Text style={[styles.footerText, { fontSize: 12 }]}>{intl.get('desc')}</Text>
            </View>
            <View style={{ paddingRight: 10, alignItems: 'flex-end', justifyContent: 'center' }}>
              <Text style={[styles.footerText, { fontSize: 12 }]}>{intl.get('total')}</Text>
            </View>
          </View>
          <ScrollView style={{ flex: 1, }}
            ref={ref => this.scrollViewRef = ref}
            onContentSizeChange={() => this.scrollViewRef.scrollToEnd()}
            showsVerticalScrollIndicator={false}>
            {
              tableItems.filter(el => !el.refRecKey).map(item => (
                <View key={item.recKey.toString()}>
                  {
                    item.lineBreak === 'Y' &&
                    <View style={{ alignItems: 'center', flexDirection: 'row', height: 20, }}>
                      <View style={{ flex: 1, height: 1, width: 20, backgroundColor: theme.grey5, marginHorizontal: 20, }}></View>
                      <Text style={{ fontFamily: 'varela-round', color: theme.grey5, fontSize: 12 }}>LINE BREAK</Text>
                      <View style={{ flex: 1, height: 1, width: 20, backgroundColor: theme.grey5, marginHorizontal: 20, }}></View>
                    </View>
                  }
                  <TouchableOpacity
                    style={{ flexDirection: 'row', marginVertical: 3, borderLeftWidth: 5, borderLeftColor: this.doTranslateTableItemColor(item), }}
                  >
                    <View style={{ paddingVertical: 10, width: 50, alignItems: 'center', borderRightColor: theme.paid }}>
                      <Text style={{ fontFamily: 'varela-round', fontSize: 16 }}>{item.orderQty}</Text>
                    </View>
                    <View style={{ paddingVertical: 10, flex: 1 }}>
                      <View style={{ flexDirection: 'row' }}>
                        <View style={{ flex: 1, }}>
                          <Text style={{ fontFamily: 'varela-round', fontSize: 16 }}>{item.menuName}</Text>
                          {
                            item.menuMod &&
                            <Text style={{ fontFamily: 'varela-round', fontSize: 16, color: theme.paid }}>{item.menuMod}</Text>
                          }
                        </View>
                        <View style={{ paddingHorizontal: 10, alignItems: 'flex-end', }}>
                          <Text style={{ fontFamily: 'varela-round', fontSize: 16 }}>{item.lineTotal?.toFixed(2)}</Text>
                        </View>
                      </View>
                      {
                        tableItems.filter(el => el.refRecKey === item.recKey).map(obj =>
                          <View style={{ marginTop: 5, flexDirection: 'row' }} key={obj.recKey.toString()}>
                            <View style={{ flex: 1, }}>
                              <Text style={{ fontFamily: 'varela-round', fontSize: 14, marginLeft: 10 }}>- {obj.menuName}</Text>
                              {
                                obj.menuMod &&
                                <Text style={{ fontFamily: 'varela-round', fontSize: 14, color: theme.paid }}>{obj.menuMod}</Text>
                              }
                            </View>
                            <View style={{ paddingHorizontal: 10, alignItems: 'flex-end', }}>
                              <Text style={{ fontFamily: 'varela-round', fontSize: 14 }}>{obj.lineTotal ? obj.lineTotal?.toFixed(2) : ''}</Text>
                            </View>
                          </View>
                        )
                      }
                    </View>
                  </TouchableOpacity>
                </View>
              ))
            }
          </ScrollView>
          <View style={{ margin: 10, marginBottom: 0, flexDirection: 'row', justifyContent: 'flex-end', alignItems: 'baseline' }}>
            <Text style={{ fontFamily: 'varela-round', fontSize: 10 }}></Text>
            <Text style={{ fontFamily: 'varela-round', fontSize: 28 }}>${selectedTable?.grandTotal?.toFixed(2) || '0.00'}</Text>
          </View>
          <View style={{ margin: 7, flexDirection: 'row', }}>

          </View>
          <View style={{ marginHorizontal: 10, height: 20, flexDirection: 'row', justifyContent: 'space-between' }}>
            <Text style={styles.footerText}>{moment().format('HH:mm')}</Text>
            <Text style={styles.footerText}>EPB BISTRO {Constants.manifest.version?.split('.')[0]}</Text>
          </View>
        </View>
        <View style={{ flex: 2, backgroundColor: theme.grey1 }}>
          <Image
            style={{ flex: 1 }}
            source={{
              uri: wall
            }}
            resizeMode='cover'
          />
        </View>
      </View >
    );
  }
}

const styles = StyleSheet.create({
  footerText: {
    fontFamily: 'varela-round',
    fontSize: 10,
    textTransform: 'uppercase',
  },
  tableUpperBand: {
    position: 'absolute',
    top: 10, left: 0,
    fontFamily: 'varela-round',
    width: '100%',
    paddingVertical: 5,
    // backgroundColor: theme.black8,
    justifyContent: 'center',
    alignItems: 'center',
    color: 'black',
    fontSize: 12,
    textAlign: 'center',
    textTransform: 'uppercase',
  },
  tableLowerBand: {
    position: 'absolute',
    bottom: 0, left: 0,
    fontFamily: 'varela-round',
    width: '100%',
    paddingVertical: 5,
    backgroundColor: theme.black8,
    justifyContent: 'center',
    alignItems: 'center',
    color: 'white',
    fontSize: 12,
    textAlign: 'center',
    textTransform: 'uppercase',
  },
  buttonText: {
    marginTop: 10,
    fontFamily: 'varela-round',
    fontSize: 10,
    color: theme.grey5,
    textTransform: 'uppercase'
  }
});
